"use client"

import { useState, useRef, useEffect } from "react"
import Link from "next/link"
import { Footer } from "@/components/footer"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Download, Save, ArrowLeft } from "lucide-react"

type ReportPageProps = {
  caseId: string
}

type ReportData = {
  diagnosis: string
  confidence: number
  modelVersion: string
  notes: string
}

export default function ReportPage({ caseId }: ReportPageProps) {
  const [report, setReport] = useState<ReportData | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [saving, setSaving] = useState(false)
  const reportRef = useRef<HTMLDivElement>(null)

  // ðŸ”¹ LOAD ACTUAL RESULTS HERE
  useEffect(() => {
    const loadReport = async () => {
      try {
        setLoading(true)
        setError(null)

        // â¬‡ï¸ change this URL to your real API route
        const res = await fetch(`/api/cases/${caseId}/report`)
        if (!res.ok) throw new Error("Failed to fetch report")

        const data = await res.json()

        setReport({
          diagnosis: data.diagnosis,
          confidence: data.confidence,
          modelVersion: data.modelVersion,
          notes: data.notes ?? "",
        })
      } catch (err) {
        console.error(err)
        setError("Failed to load report data")
      } finally {
        setLoading(false)
      }
    }

    loadReport()
  }, [caseId])

  const handleSave = async () => {
    if (!report) return
    setSaving(true)
    try {
      // â¬‡ï¸ update this to match your backend (PUT/PATCH/etc.)
      const res = await fetch(`/api/cases/${caseId}/report`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(report),
        })
      if (!res.ok) throw new Error("Failed to save")

      alert("Report saved successfully")
    } catch (err) {
      console.error(err)
      alert("Failed to save report")
    } finally {
      setSaving(false)
    }
  }

  const handleExportPDF = () => {
    // TODO: wire up real PDF export using reportRef
    alert(`Exporting report for case ${caseId} as PDFâ€¦`)
  }

  if (loading) {
    return (
      <div className="flex min-h-screen flex-col">
        <main className="flex-1 px-6 py-4 flex items-center justify-center">
          <p className="text-sm text-muted-foreground">Loading reportâ€¦</p>
        </main>
        <Footer />
      </div>
    )
  }

  if (error || !report) {
    return (
      <div className="flex min-h-screen flex-col">
        <main className="flex-1 px-6 py-4 flex flex-col items-center justify-center gap-3">
          <p className="text-sm text-red-500">{error ?? "No report found"}</p>
          <Link href="/cases">
            <Button variant="outline">Back to cases</Button>
          </Link>
        </main>
        <Footer />
      </div>
    )
  }

  return (
    <div className="flex min-h-screen flex-col">
      <main className="flex-1 px-6 py-4">
        <div className="mb-4 flex items-center gap-3">
          <Link href="/cases">
            <Button variant="ghost" size="icon">
              <ArrowLeft className="h-4 w-4" />
            </Button>
          </Link>
          <div>
            <h1 className="text-xl font-semibold">Case Report</h1>
            <p className="text-sm text-muted-foreground">
              Case ID: <span className="font-mono">{caseId}</span>
            </p>
          </div>
        </div>

        <div className="mb-4 flex items-center gap-3">
          <Button onClick={handleSave} disabled={saving}>
            <Save className="mr-2 h-4 w-4" />
            {saving ? "Saving..." : "Save Report"}
          </Button>
          <Button variant="outline" onClick={handleExportPDF}>
            <Download className="mr-2 h-4 w-4" />
            Export PDF
          </Button>
        </div>

        <div
          ref={reportRef}
          className="space-y-4 rounded-lg border bg-card p-4 shadow-sm"
        >
          <div className="grid gap-4 md:grid-cols-3">
            <div>
              <label className="mb-1 block text-xs font-medium text-muted-foreground">
                Diagnosis
              </label>
              <Input
                value={report.diagnosis}
                onChange={(e) =>
                  setReport((r) => (r ? { ...r, diagnosis: e.target.value } : r))
                }
              />
            </div>
            <div>
              <label className="mb-1 block text-xs font-medium text-muted-foreground">
                Confidence (%)
              </label>
              <Input
                type="number"
                value={report.confidence}
                onChange={(e) =>
                  setReport((r) =>
                    r
                      ? { ...r, confidence: Number(e.target.value) || 0 }
                      : r
                  )
                }
              />
            </div>
            <div>
              <label className="mb-1 block text-xs font-medium text-muted-foreground">
                Model Version
              </label>
              <Input
                value={report.modelVersion}
                onChange={(e) =>
                  setReport((r) =>
                    r ? { ...r, modelVersion: e.target.value } : r
                  )
                }
              />
            </div>
          </div>

          <div>
            <label className="mb-1 block text-xs font-medium text-muted-foreground">
              Notes
            </label>
            <textarea
              className="min-h-[120px] w-full rounded-md border bg-background px-3 py-2 text-sm focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
              value={report.notes}
              onChange={(e) =>
                setReport((r) => (r ? { ...r, notes: e.target.value } : r))
              }
              placeholder="Add radiologist notes, differential diagnosis, follow-up recommendations, etc."
            />
          </div>

          <div className="flex justify-between border-t pt-2 text-xs text-muted-foreground">
            <span>Generated by Lung Model {report.modelVersion}</span>
            <span>Case: {caseId}</span>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  )
}